import mainLogo from "./assets/mainlogo.svg";
import DodoMainFrame from "./modules/dodoMainFrame";
import DodoSearch from "./modules/dodoSearch";
import DodoLibLabel from "./modules/dodoLibLabel";
import { useState } from "react";

function DoDoLogo() {
  //   
  const [keyword, setKeyword] = useState([]);
  //   
  const [libInfo, setLibInfo] = useState([]);

  // 
  const values = { keyword: keyword, library: libInfo };

  return (
    <div className="flex-container flex-column mx-auto fade-in-box-dodo" style={{ width: "80%" }}>
      <div className="d-flex mx-auto align-items-end" style={{ flexBasis: "35%" }}>
        <img className="titleLogo" src={mainLogo} alt="" />
      </div>
      <div className="flex-container mx-auto mb-2" style={{ flexBasis: "10%" }}>
        <DodoSearch placeholder=" . ex) , SQL" setCheckedInputs={setKeyword} values={values} />
      </div>
      <div className="flex-container mx-auto " style={{ flexBasis: "60%" }}>
        <DodoLibLabel checkedInputs={libInfo} setCheckedInputs={setLibInfo} />
        {console.log(libInfo)}
      </div>
    </div>
  );
}

// Main Function
const DoDoMain = () => {
  return <DodoMainFrame children={DoDoLogo()} />;
};

export default DoDoMain;
